*{box-sizing:border-box;margin:0;padding:0}body{font-family:"Inter",sans-serif;background-color:#f8f9fa;color:#212529}.btn{width:100%;background-color:#dfdfdf;border:none;padding:.75rem;border-radius:6px;cursor:pointer;transition:all .2s ease-in-out}.btn:hover{background-color:#d4d4d4}.btn:focus{background-color:#ccc}.btn-primary{background-color:#0092b8;color:#fff}.btn-primary:hover{background:#104e64}.btn-primary:focus{background:#0092b8}.form-control{width:100%;padding:.75rem;margin-bottom:1rem;border-radius:6px;border:.1rem solid #ccc;outline:none;transition:all .2s ease-in-out}.form-control:hover{border-color:#104e64}.form-control:focus{border-color:#0092b8}